html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

html { -ms-text-size-adjust: 100%; -webkit-text-size-adjust: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

input[type="submit"], button { cursor: pointer; }

input[type="submit"]::-moz-focus-inner, button::-moz-focus-inner { padding: 0; border: 0; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

body { font-family: 'Roboto Regular', sans-serif; background: #fff; background-size: cover; font-size: 14px; line-height: 1.4; color: #000; }

[v-cloak] { display: none; }

a { font-family: "Roboto Regular", sans-serif; font-size: 14px; color: #323232; text-decoration: none; }

a:hover { color: #3db0ff; }

.container { max-width: 1240px; margin: 0 auto; }

@media only screen and (max-width: 1270px) { .container { margin: 0 20px; } }

.container-inner { display: flex; }

.content { margin-left: 20px; margin-top: 20px; width: 100%; }

@media only screen and (max-width: 760px) { .content { margin-left: 0; } }

@media only screen and (max-width: 550px) { .content { margin-top: 20px; } }

.btn-blue { background-color: #ff646f; padding: 10px 37px; border-radius: 20px; color: #fff; font-family: 'Roboto Medium', sans-serif; font-size: 14px; }

.btn-blue:hover { background-color: #0A87E1; transition: all 0.5s; color: #fff; }

.slider-top-wr { min-height: 100vh; background-color: #FF3CAC; background-image: linear-gradient(225deg, #FF3CAC 0%, #784BA0 50%, #2B86C5 100%); position: relative; }

.slider-top-wr .slick-dotted.slick-slider { margin-bottom: 0; }

.slider-top-wr:after { content: ''; position: absolute; left: 0; top: 0; width: 100%; height: 100vh; background-color: #000; opacity: 0.5; z-index: -1; }

.slider-top { overflow: hidden; min-height: 100vh; }

@media only screen and (max-width: 550px) { .slider-top .slick-next, .slider-top .slick-prev { display: none !important; } }

.slider-top .slick-next:before, .slider-top .slick-prev:before { font-size: 0; }

.slider-top .slick-dots { bottom: 40px; }

.slider-top .slick-dots li.slick-active button:before { color: #e70368; opacity: 100; }

.slider-top .slick-dots li button:before { color: #3db0ff; opacity: 100; }

.slider-top .slick-prev { left: 50px; background-image: url("../img/left-arrow.png"); width: 32px; height: 60px; z-index: 300; }

.slider-top .slick-prev:hover { left: 50px; background-image: url("../img/left-arrow.png"); width: 32px; height: 60px; z-index: 300; }

.slider-top .slick-next { right: 50px; background-image: url("../img/right-arrow.png"); width: 32px; height: 60px; z-index: 20; }

.slider-top .slick-next:hover { right: 50px; background-image: url("../img/right-arrow.png"); width: 32px; height: 60px; z-index: 20; }

.slider-top__item { background-position: 50% 50%; background-repeat: no-repeat; background-size: cover; height: 100vh !important; position: relative; z-index: -1; }

.slider-top__item:after { content: ''; position: absolute; left: 0; top: 0; width: 100%; height: 100vh; background-color: #000; opacity: 0.5; }

.slider-top__item:before { content: ''; position: absolute; top: 0; left: 0; width: 100%; height: 100vh; background-color: #FF3CAC; background-image: linear-gradient(225deg, #FF3CAC 0%, #784BA0 50%, #2B86C5 100%); opacity: 0.7; }

.slider-top__item .desc { position: absolute; top: 0; left: 0; width: 100%; height: 100vh; z-index: 20; display: flex; flex-direction: column; justify-content: center; align-items: center; }

.slider-top__item .desc .big { font-family: 'Roboto Bold',sans-serif; font-size: 50px; color: #fff; font-weight: 600; }

@media only screen and (max-width: 700px) { .slider-top__item .desc .big { font-size: 31px; } }

.slider-top__item .desc .small { font-family: 'Roboto Light',sans-serif; font-size: 21px; color: #fff; padding-bottom: 20px; }

@media only screen and (max-width: 700px) { .slider-top__item .desc .small { font-size: 16px; } }

.slider-top__item .desc a { color: #fff; border: 1px solid #fff; border-radius: 20px; padding: 8px 50px; }

@media only screen and (max-width: 700px) { .slider-top__item .desc a { padding: 8px 30px; font-size: 12px; } }
